import React, { useState, useCallback } from 'react';
import {
  Container,
  Typography,
  Paper,
  Box,
  Button,
  Alert,
  CircularProgress,
  Card,
  CardContent,
  Chip,
  Grid,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  TextField,
} from '@mui/material';
import {
  CloudUpload,
  VideoFile,
  CheckCircle,
  Error as ErrorIcon,
} from '@mui/icons-material';
import { useDropzone } from 'react-dropzone';
import { useMutation, useQuery } from '@tanstack/react-query';
import { useNavigate } from 'react-router-dom';

import { jobsApi, filesApi } from '../services/api';
import { submitGhostCutJob, getJobStatus } from '../services/ghostcutApi';
import { useAuth } from '../contexts/AuthContext';

const UploadPage: React.FC = () => {
  const navigate = useNavigate();
  const { user } = useAuth();
  const [selectedFile, setSelectedFile] = useState<File | null>(null);
  const [displayName, setDisplayName] = useState('');
  const [uploadError, setUploadError] = useState('');
  const [isProcessing, setIsProcessing] = useState(false);
  const [processingProgress, setProcessingProgress] = useState(0);
  const [processingMessage, setProcessingMessage] = useState('');
  const [downloadUrl, setDownloadUrl] = useState('');
  const [processedJobId, setProcessedJobId] = useState('');


  // File upload and process mutation (combined endpoint)
  const uploadFileMutation = useMutation({
    mutationFn: async (file: File) => {
      // Upload file and automatically start processing
      const response = await jobsApi.uploadAndProcess(file, displayName || file.name);
      return response;
    },
    onError: (error: any) => {
      console.error('Upload and process error:', error);
      setUploadError(error.response?.data?.detail || 'Upload and processing failed');
      setIsProcessing(false);
    },
  });


  const onDrop = useCallback((acceptedFiles: File[]) => {
    const file = acceptedFiles[0];
    if (file) {
      setSelectedFile(file);
      setDisplayName(file.name.replace(/\.[^/.]+$/, '')); // Remove extension
      setUploadError('');
    }
  }, []);

  const { getRootProps, getInputProps, isDragActive, isDragReject } = useDropzone({
    onDrop,
    accept: {
      'video/*': ['.mp4', '.avi', '.mov', '.mkv', '.webm'],
    },
    maxFiles: 1,
    maxSize: 1000 * 1024 * 1024, // 1GB
  });

  const handleSubmit = async () => {
    if (!selectedFile) return;

    setIsProcessing(true);
    setUploadError('');
    setProcessingProgress(10);
    setProcessingMessage('Uploading video...');

    try {
      // Upload file and start GhostCut processing in one step
      const uploadResult = await uploadFileMutation.mutateAsync(selectedFile);
      
      // The uploadResult should contain the job info
      if (uploadResult && uploadResult.id) {
        setProcessedJobId(uploadResult.id);
        setProcessingProgress(20);
        setProcessingMessage('Video uploaded, starting text removal...');
        
        // Start polling for the job status
        pollJobStatus(uploadResult.id);
      } else {
        throw new Error('Failed to get job ID from upload');
      }
      
    } catch (error) {
      console.error('Processing failed:', error);
      setUploadError(error instanceof Error ? error.message : 'Processing failed');
      setIsProcessing(false);
    }
  };

  const pollJobStatus = async (jobId: string) => {
    // No timeout - keep polling until complete or failed
    let pollCount = 0;

    const poll = async () => {
      try {
        // Use the backend jobs API to get status
        const job = await jobsApi.getJob(jobId);
        
        setProcessingProgress(job.progress_percentage || 20);
        
        // Update message to show we're still processing
        pollCount++;
        const baseMessage = job.progress_message || 'Processing video...';
        const stillWorkingMessage = pollCount > 12 ? ' (Still working, please wait...)' : '';
        setProcessingMessage(baseMessage + stillWorkingMessage);
        
        if (job.status === 'completed') {
          // Get the output file download URL
          const outputUrl = `${process.env.REACT_APP_API_URL || 'http://localhost:8000/api/v1'}/files/download/${jobId}`;
          setDownloadUrl(outputUrl);
          setProcessingProgress(100);
          setProcessingMessage('Processing completed! Your video is ready for download.');
          setIsProcessing(false);
          return;
        }
        
        if (job.status === 'failed') {
          throw new Error(job.error_message || 'Processing failed');
        }
        
        // Keep polling indefinitely while processing
        if (job.status === 'processing' || job.status === 'queued') {
          setTimeout(poll, 5000); // Poll every 5 seconds
        }
        
      } catch (error) {
        setUploadError(error instanceof Error ? error.message : 'Processing failed');
        setIsProcessing(false);
      }
    };

    poll();
  };

  const handleRemoveFile = () => {
    setSelectedFile(null);
    setDisplayName('');
    setUploadError('');
    setIsProcessing(false);
    setProcessingProgress(0);
    setProcessingMessage('');
    setDownloadUrl('');
    setProcessedJobId('');
  };

  const handleDownload = () => {
    if (downloadUrl) {
      const link = document.createElement('a');
      link.href = downloadUrl;
      link.download = `processed_${displayName || selectedFile?.name || 'video'}.mp4`;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    }
  };

  const formatFileSize = (bytes: number) => {
    if (bytes === 0) return '0 Bytes';
    const k = 1024;
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(k));
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i];
  };

  const getVideoDuration = (file: File): Promise<number> => {
    return new Promise((resolve) => {
      const video = document.createElement('video');
      video.preload = 'metadata';
      video.onloadedmetadata = () => {
        resolve(video.duration);
      };
      video.src = URL.createObjectURL(file);
    });
  };

  const estimateCredits = (file: File) => {
    // Basic estimation - 10 credits per minute
    return getVideoDuration(file).then(duration => Math.ceil(duration / 60) * 10);
  };

  return (
    <Container maxWidth="lg" sx={{ py: 4 }}>
      <Typography variant="h4" gutterBottom fontWeight={600}>
        Upload Video for Text Removal
      </Typography>
      <Typography variant="body1" color="text.secondary" paragraph>
        Upload your video and we'll automatically remove text, subtitles, and watermarks using AI.
      </Typography>

      {/* User Credits Display */}
      <Alert severity="info" sx={{ mb: 4 }}>
        <Box sx={{ display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
          <Typography>
            Available Credits: <strong>{user?.credits_balance || 0}</strong>
          </Typography>
          <Chip
            label={`${user?.subscription_tier || 'Free'} Plan`}
            color="primary"
            variant="outlined"
          />
        </Box>
      </Alert>

      <Grid container spacing={4}>
        <Grid item xs={12} md={8}>
          {/* File Upload Area */}
          <Paper
            {...getRootProps()}
            sx={{
              p: 4,
              textAlign: 'center',
              cursor: 'pointer',
              borderStyle: 'dashed',
              borderWidth: 2,
              borderColor: isDragActive ? 'primary.main' : 
                          isDragReject ? 'error.main' : 'grey.300',
              backgroundColor: isDragActive ? 'primary.light' : 
                              isDragReject ? 'error.light' : 'transparent',
              transition: 'all 0.2s ease',
              '&:hover': {
                borderColor: 'primary.main',
                backgroundColor: 'primary.light',
              },
            }}
          >
            <input {...getInputProps()} />
            {selectedFile ? (
              <Box>
                <VideoFile sx={{ fontSize: 64, color: 'primary.main', mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  {selectedFile.name}
                </Typography>
                <Typography variant="body2" color="text.secondary" gutterBottom>
                  {formatFileSize(selectedFile.size)}
                </Typography>
                <Button
                  variant="outlined"
                  onClick={(e) => {
                    e.stopPropagation();
                    handleRemoveFile();
                  }}
                  sx={{ mt: 2 }}
                >
                  Remove File
                </Button>
              </Box>
            ) : (
              <Box>
                <CloudUpload sx={{ fontSize: 64, color: 'grey.400', mb: 2 }} />
                <Typography variant="h6" gutterBottom>
                  {isDragActive
                    ? 'Drop the video here...'
                    : 'Drag & drop a video, or click to select'
                  }
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  Supports MP4, AVI, MOV, MKV, WebM (max 1GB)
                </Typography>
              </Box>
            )}
          </Paper>

          {uploadError && (
            <Alert severity="error" sx={{ mt: 2 }}>
              {uploadError}
            </Alert>
          )}

          {/* Processing Configuration */}
          {selectedFile && (
            <Card sx={{ mt: 3 }}>
              <CardContent>
                <Typography variant="h6" gutterBottom>
                  Processing Configuration
                </Typography>
                
                <TextField
                  fullWidth
                  label="Display Name"
                  value={displayName}
                  onChange={(e) => setDisplayName(e.target.value)}
                  margin="normal"
                  helperText="Give your processed video a custom name"
                />

                <Box sx={{ mt: 2, p: 2, backgroundColor: 'primary.light', borderRadius: 1 }}>
                  <Typography variant="subtitle2" sx={{ mb: 1, color: 'primary.contrastText' }}>
                    Basic Text Removal
                  </Typography>
                  <Typography variant="body2" sx={{ color: 'primary.contrastText', opacity: 0.8 }}>
                    Standard text inpainting for subtitles and overlays
                  </Typography>
                </Box>

                {!isProcessing && !downloadUrl && (
                  <Button
                    fullWidth
                    variant="contained"
                    size="large"
                    onClick={handleSubmit}
                    disabled={!selectedFile}
                    sx={{ mt: 3 }}
                  >
                    START PROCESSING
                  </Button>
                )}

                {isProcessing && (
                  <Box sx={{ mt: 3 }}>
                    <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                      <CircularProgress 
                        variant="determinate" 
                        value={processingProgress} 
                        sx={{ mr: 2 }} 
                      />
                      <Box>
                        <Typography variant="body2" fontWeight={600}>
                          {processingProgress}% Complete
                        </Typography>
                        <Typography variant="caption" color="text.secondary">
                          {processingMessage}
                        </Typography>
                      </Box>
                    </Box>
                  </Box>
                )}

                {downloadUrl && (
                  <Button
                    fullWidth
                    variant="contained"
                    size="large"
                    onClick={handleDownload}
                    sx={{ mt: 3, backgroundColor: 'success.main' }}
                  >
                    DOWNLOAD PROCESSED VIDEO
                  </Button>
                )}
              </CardContent>
            </Card>
          )}
        </Grid>

        <Grid item xs={12} md={4}>
          {/* Processing Info */}
          <Card>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                How It Works
              </Typography>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <CheckCircle color="primary" sx={{ mr: 2 }} />
                <Typography variant="body2">
                  AI automatically detects text in your video
                </Typography>
              </Box>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <CheckCircle color="primary" sx={{ mr: 2 }} />
                <Typography variant="body2">
                  Advanced inpainting removes text seamlessly
                </Typography>
              </Box>
              
              <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
                <CheckCircle color="primary" sx={{ mr: 2 }} />
                <Typography variant="body2">
                  Download your processed video
                </Typography>
              </Box>
            </CardContent>
          </Card>

          {/* Pricing Info */}
          <Card sx={{ mt: 3 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Credit Usage
              </Typography>
              
              <Typography variant="body2" paragraph>
                Processing costs approximately 10 credits per minute of video.
              </Typography>
              
              <Box sx={{ backgroundColor: 'grey.50', p: 2, borderRadius: 1 }}>
                <Typography variant="caption" color="text.secondary">
                  Example: A 3-minute video costs ~30 credits
                </Typography>
              </Box>
            </CardContent>
          </Card>

          {/* Supported Formats */}
          <Card sx={{ mt: 3 }}>
            <CardContent>
              <Typography variant="h6" gutterBottom>
                Supported Formats
              </Typography>
              
              <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 1 }}>
                {['MP4', 'AVI', 'MOV', 'MKV', 'WebM'].map((format) => (
                  <Chip
                    key={format}
                    label={format}
                    size="small"
                    variant="outlined"
                  />
                ))}
              </Box>
              
              <Typography variant="body2" sx={{ mt: 2 }} color="text.secondary">
                Maximum file size: 1GB
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};

export default UploadPage;